    "text":
        "I lived in San Jose California  the heart of Silicon Valley  on and off for 2 years (the longest about 6 months at a time). The Bay Area is an amazing place - essentially a string of cities all merged into one another stretching around the San Francisco Bay. With a population of over 7.5 million, glorious weather, and iconic surroundings  of both the urban and natural type  you really get this sense of being at the centre of *something*. And it is certainly the centre of the tech world, for which it has long been famous for. Silicon Valley  or more accurately, Santa Clara Valley  stretches from San Jose to Redwood City, just short of San Francisco - though the definition is blurred somewhat considering San Francisco is just as much a part of Silicon Valley. While San Jose is by far the biggest city in Silicon Valley (even bigger than San Francisco), some of the smaller cities like Palo Alto are equally impressive and important. Everywhere you go, the presence of tech is strongly felt  whether it is the developers you are talking to in the coffee shops, the giant headquarters of famous tech companies towering over you, or the driverless-vehicles and robot dogs passing you by on the street.  ",
    "text1":
        "There are literally hundreds  if not thousands  of tech companies based in the Bay Area and I barely scratched the surface. However, I did spend a day or two driving throughout Silicon Valley visiting some of the most famous sites and Im sharing some highlights of that here today! Like I said, I was based in San Jose where there were lots of famous HQs, including Paypal, Ebay, Adobe and Netflix. To be honest there's not much to do at these places other than get a quick snap outside. Plus San Jose is enormous and the companies are quite spread out, with Paypal in the North and Netflix in the South. San Jose blurs into Santa Clara, where there are other big companies like Microsoft and Intel, as well as a really nice University. Cupertino, which is beside Santa Clara, is famous for the Apple HQ. I didn't visit Apple because the new HQ was under construction while I was in the area, although I did visit 'Steve Jobs Garage' in Los Altos - the legendary childhood home where he supposedly started Apple with Steve Wozniak. I must say I sensed a growing anti-tech sentiment in the South Bay (San Jose/Santa Clara/Cupertino area), where the large working and middle class populations were feeling squeezed out as a result of these corporations - and the influx of their workers to the area - bought up vaste swathes of land and property, pricing the locals out.",
